import {
  Stack, HStack, Spacer, Box, Text, Link,
  Image, IconButton,
  useDisclosure,
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
import React, { useState, useEffect } from 'react';

const Links = ['Home', 'Aktuelles', 'Leistungen', 'Kontakt', 'Karriere'];

const NavLink = ({ children }) => (
  <Link
    px={2}
    py={1}
    rounded={'md'}
    _hover={{
      textDecoration: 'none',
    }}
    href={'#'}>
    {children}
  </Link>
);

export default function NefinoNavbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [scrolling, setScrolling] = useState(false);

  useEffect(() => {

    function onScroll() {
      window.pageYOffset > 1 ? setScrolling(true) : setScrolling(false);
    }

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  });

  let logo = ""
  {
    (scrolling || isOpen) ? logo = "https://www.nefino.de/wp-content/uploads/2021/02/Nefino_logo.png"
      : logo = "https://www.nefino.de/wp-content/uploads/2021/02/Nefino_white.svg"
  }

  return (
    <>

      <Box px={4} py={3} width="100%" position="fixed" zIndex="1000"
        color={(scrolling || isOpen) && "blue.600"}
        bg={(scrolling || isOpen) && "white"}
      >

        <HStack h={10} >
          <Link p="2.5" ml="2.5">
            <Image
              width="153px"
              height="40px"
              //src="https://www.nefino.de/wp-content/uploads/2021/02/Nefino_logo.png"
              //src="https://www.nefino.de/wp-content/uploads/2021/02/Nefino_white.svg"
              src={logo}
            />
          </Link>

          <Spacer />

          <HStack
            as={'nav'}
            spacing={[1, 2, 3]}
            display={{ base: 'none', md: 'flex' }}>
            {Links.map((link) => (
              <NavLink key={link}>{link}</NavLink>
            ))}
            <NavLink>
              <Image
                width="2vw"
                height="14px"
                src="data:image/png;base64,"
              />
            </NavLink>
            <NavLink>
              <Image
                width="2vw"
                height="14px"
                src="data:image/png;base64,"
              />
            </NavLink>
          </HStack>


          <IconButton
            bg="transparent"
            size={'md'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ md: 'none' }}
            onClick={isOpen ? onClose : onOpen}
            _hover={{
              textDecoration: 'none',
            }}
            _active={{
              textDecoration: 'none',
            }}
            _focus={
              { outLine: "0" }
            }
          />
          <Text style={{ marginInlineStart: '0' }} display={{ md: 'none' }}>Menü</Text>

        </HStack>

        {isOpen ? (
          <Box p={4} my={2} display={{ md: 'none' }} bg="white" color="blue.700" position="absolute" left="0" width="100%" style={{ zIndex: "1" }}>
            <Stack as={'nav'} spacing={4}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
              <NavLink>
                <Image
                  width="4vw"
                  height="14px"
                  src="data:image/png;base64,"
                />
              </NavLink>
              <NavLink>
                <Image
                  width="4vw"
                  height="14px"
                  src="data:image/png;base64,"
                />
              </NavLink>
            </Stack>
          </Box>
        ) : null}
      </Box>



    </>
  );
}